canvas {
	border: 10px solid white;
	border-radius: 4px;
}

body {
	font-family: CodenewRoman Nerd Font, Ubuntu, sans-serif;
	margin: 0;
	padding: 0;
	min-height: 100%;
	min-width: 100%;
	color: #2c2c2c;
	background-color: #dcdcdc;
}

h1 {
	font-size: 3rem;
	font-weight: bolder;
}

h2 {
	font-size: 2.5rem;
	font-weight: bold;
}

h3 {
	font-size: 2rem;
	font-weight: bold;
}

.grid {
	display: grid;

	grid-gap: 2rem;
	grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
	grid-template-rows: 
		1fr 
		2fr 
		2fr 
		2fr;
	grid-template-areas: 
		". \9title \9title \9title \9."
		". \9  board \9next \9score \9."
		". \9  board \9next \9state \9."
		". \9  board \9next \9menu \9.";
}

.main-header {
	grid-area: title;
}

.board {
	grid-area: board;
}
.next {
	grid-area: next;
}

.title {
	grid-area: title;
}

.score {
	grid-area: score;
}

.state {
	grid-area: state;
}

.menu {
	grid-area: menu;
}

.box {
	border-radius: 5px;
	border: 1px solid gray;
	box-shadow: 
		0 2px 3px rgba(0, 0, 0, 0.1), 
		0 0 0 1px rgba(0, 0, 0, 0.1);
	padding: 20px;
}

.has-background-info {
	background-color: #07b2fc;
}

.has-background-warning {
	background-color: #fceb05;
}

.has-text-centered {
	text-align: center;
}
/****************************************/
/* BASIC BUTTON 						*/
/****************************************/

.button {
	border-radius: 5px;
	border-width: 2px;
	margin: 0.5rem 0;
	color: black;
	-webkit-align-content: center;
	        align-content: center;
	text-align: center;
	-webkit-transition-duration: 0.5s;
	        transition-duration: 0.5s;
	box-shadow: none;
}

.button:hover {
	box-shadow: 
		0 2px 5px rgba(0, 0, 0, 0.1), 
		0 0 0 2px rgba(0, 0, 0, 0.1);
	color: white;
	background-color: #000;
}
/****************************************/
/*BASIC BUTTON WITH IS-INFO MODIFIER 	*/
/****************************************/

.button.is-info {
	border-color: #07b2fc;
}

.button.is-info:hover {
	color: black;
	background-color: #07b2fc;
}

/****************************************/
/*BASIC BUTTON WITH IS-SUCESS MODIFIER 	*/
/****************************************/

.button.is-success {
	border-color: #0afc02;
	color: #0afc02;
}

.button.is-success:hover {
	color: black;
	background-color: #0afc02;
}

/****************************************/
/*BASIC BUTTON WITH IS-ERROR MODIFIER 	*/
/****************************************/

.button.is-error {
	border-color: #fc0206;
	color: #fc0206;
}

.button.is-error:hover {
	color: black;
	background-color: #fc0206;
}

/****************************************/
/* BASIC MODIFIERS 			 			*/
/****************************************/

.is-fullwidth {
	width: 100%;
}

.is-visible {
	display: block !important;
}

/****************************************/
/* BASIC MODAL 							*/
/****************************************/

.modal {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgba(0, 0, 0, 0.3);
}

.modal-content {
	position: absolute;
	left: 40%;
	top: 40%;
	width: 20%;
	background-color: #fff;
	border-radius: 10px;
	border: 1px solid gray;
	box-shadow: 
		0 2px 3px rgba(0, 0, 0, 0.1), 
		0 0 0 1px rgba(0, 0, 0, 0.1);
	padding: 20px;

}
